@font-face {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 400;
    src: url('./assets/fonts/mulish-v12-latin-regular.woff2') format('woff2'),
    url('./assets/fonts/mulish-v12-latin-regular.woff') format('woff');
}

@font-face {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 600;
    src: url('./assets/fonts/mulish-v12-latin-600.woff2') format('woff2'),
    url('./assets/fonts/mulish-v12-latin-600.woff') format('woff');
}

@font-face {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 700;
    src: url('./assets/fonts/mulish-v12-latin-700.woff2') format('woff2'),
    url('./assets/fonts/mulish-v12-latin-700.woff') format('woff');
}

body {
    cursor: url(./assets/icons/cursor.svg ) , auto;
    margin: 0;
    padding: 0;
    font-family: 'Mulish';
    padding-left: 255px;
}

#root {
    width: 100%;
}

ul {
    list-style-type: none;
    text-decoration: none;
}

li a p {
    list-style-type: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
}
