.header__wrapper {
    display: flex;
    position: absolute;
    right: 33px;
    top: 19px;
}

.header__list {
    display: flex;
    gap: 30px;
    
    .q {
        padding-right: 32px;
        border-right: 1px solid #DFE0EB;
        display: flex;
        gap: 28px;
    }
    
    .w {
        display: flex;
        gap: 14px;
    }
    
}

.header__btn {
    position: absolute;
    left: -903px;
    top: 50px;
    outline: none;
    border: none;
    padding: 4px 7px;
    background: none;
    background-color: none;
    color: black;
    transition: all 0.4s ease;
    
}

.header__item {
    display: flex;
    align-items: center;
}

.header__img {
    border-radius: 50%;
    background-color: #363740;
}