.overview {
    width: 100%;
    padding-top: 128px;
    
}

a {
    list-style-type: none;
    text-decoration: none;
    width: auto;
    height: auto;
}

h3 ,h5, p {
    margin: 0;
}

.overview__header {
    
}

.overview__top {
    
}

.overview__t-list {
    display: flex;
    gap: 30px;
    padding: 0;
    justify-content: space-between;
}

.overview__t-item {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.overview__t-text {
    
}

.overview__t-list {
    
}

.overview__t-item {
    background: #FFFFFF;
    border: 1px solid #DFE0EB;
    border-radius: 8px;
    
    padding: 24px 32px;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    background: #FFFFFF;
    border-radius: 8px;
    margin-left: auto;
    margin-right: auto;
    color: black;
    transition: all ease 0.3s;

    &:hover {
        // border: 1px solid #3751FF;
        box-shadow: 0 0 0 1px #3751FF;
        color: #3751FF;
    }
}

.overview__t-text {
    font-weight: 700;
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 0.4px;
    width: 194px;
}

.overview__t-header {
    height: 50px;
    left: 320px;
    right: 608px;
    top: 60px;
    font-weight: 700;
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 1px;
    width: 194px;
    
    &:active {
        color: #3751FF;
    }
    &:focus {
        color: #3751FF;
    }
}

.overview__center {
    margin-top: 32px;
    transition: all 0.4s ease;
    
}

.overview__center {
    background: #FFFFFF;
    border: 1px solid #DFE0EB;
    border-radius: 8px;
    padding-left: 32px;
}

.overview__c-wrapper {
    display: flex;
    justify-content: space-between;
}

.overview__c-left {
    width: 780px;
    
}
.overview__c-leftt {
    display: flex;
    margin-right: 144px;
    div {
        display: flex;
    }
}

.overview__c-date {
    width: 400px;
    margin-right: 144px;
    align-items: center;
}

.overview__c-text1 {
    font-family: 'Mulish';
    font-weight: 700;
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 0.4px;
    color: #252733;
    margin-top: 0;
    margin-top: 32px;
    margin-bottom: 8px;
}

.overview__c-text2 {
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.1px;
    color: #9FA2B4;
    margin: 0;
    height: 15px;
    display: flex;
    align-items: center;
}

.overview__c-text3 {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.1px;
    color: #9FA2B4;
    display: flex;
    hr {
        width: 16px;
        height: 1px;
        background-color: #3751FF;
    }
}

.overview__c-text4 {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.1px;
    color: #9FA2B4;
    display: flex;
    hr {
        width: 16px;
        height: 1px;
        background-color: #DFE0EB;
    }
}

.overview__c-w {
    display: flex;
    gap: 32px;
}

// 

.overview__r-list {
    margin: 0;
    padding: 0;
    width: 342px;
    .q {
        
        border-start-end-radius:8px ;
    }
    .t {
        border-end-end-radius:8px ;
    }
}

.overview__r-item {
    padding: 33px;
    padding-bottom: 24px;
    display: flex;
    flex-direction: column;
    text-align: center;
    border: 1px solid #DFE0EB;
    gap: 6px;
    
    p {
        margin: 0;
    }
    h3 {
        margin: 0;
    }
}

.overview__r-text {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.3px;
    color: #9FA2B4;
}

.overview__r-header {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    letter-spacing: 0.3px;
    color: #252733;
}

.overview__b-wrapper-bottom {
    display: flex;
    justify-content: space-between;
}

.overview__b-right {
    display: flex;
    gap: 30px;
    justify-content: space-between;
    
}

.overview__b-header {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 24px;
    color: #252733;
}

.overview__b-text {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #3751FF;
}

.overview__b-support {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #9FA2B4;
    margin-top: 8px;
    margin-bottom: 15px;
}

.overview__b-list {
    width: 546px;
    background: #FFFFFF;
    border: 1px solid #DFE0EB;
    border-radius: 8px;
    padding-top: 32px;
    padding-left: 0;
    .h {
        border-bottom: none;
    }
    
    .Urgent {
        background: #FEC400;
        border-radius: 8px;
        padding: 5px 12px;
    }
    .New {
        background: #29CC97;        
        border-radius: 8px;
        padding: 5px 12px;
    }
    .Default {
        background: #F0F1F7;
        border-radius: 8px;
        padding: 5px 12px;
    }
}

.overview__b-item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #DFE0EB;
    padding: 20px 32px 18px 32px;
    align-items: center;
}

.overview__b-item-top {
    padding-left: 32px;
    padding-right: 32px;
}