.tickets {
    width: 100%;
    margin-top: 128px;
    background: #FFFFFF;
    border: 1px solid #DFE0EB;
    border-radius: 8px;
    transition: all 0.4s ease;
}

.tickets__i-header {
    font-family: 'Mulish';
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #252733;
    width: 400px;
    margin-left: 24px;
    
}
.tickets__i-name {
    font-family: 'Mulish';
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #252733;
    width: 208px;
}
.tickets__i-data {
    font-family: 'Mulish';
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    width: 140px;
    color: #252733;
}

.tickets__wrapper {
    width: 1120px;
}

.tickets__list {
    padding: 0;
    width: 100%;
}


.tickets__wrapper {
    width: 100%;
}

.tickets__top {
    display: flex;
    justify-content: space-between;
    width: 100%;
    
    div {
        display: flex;
        align-items: center;
        justify-content: right;
        gap: 56px;
        width: 100%;
        padding-left: 32px ;
        padding-right: 32px ;
        
    }
}

.tickets__t-header {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 0.4px;
    width: 400px;
    color: #252733;
}

.tickets__sort {
    display: flex;
    gap: 9px;
}

.tickets__filter {
    display: flex;
    gap: 10px;
}

.tickets__list {
    li:hover {
        background-color: #F7F8FF;
    }
}

.tickets__item {
    display: flex;
    align-items: center;
    border-top: 1.5px solid #DFE0EB;
    justify-content: space-between;
    padding: 24px 31px;
    span {
        margin-left: 52px;
        font-family: 'Mulish';
        font-style: normal;
        font-weight: 700;
        font-size: 11px;
        line-height: 14px;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.5px;
        text-transform: uppercase;
    }
    
}