.d-flex {
    display: flex;
}

.sidebar__list {
    position: fixed;
    left: 0px;
}

.sidebar__list {
    display: flex;
    flex-direction: column;
    width: 255px;
    height: 1300px;
    margin: 0;
    padding: 0;
    background-color: #363740;
    transition: all 0.4s ease;
}

.sidebar__top-img {
    margin: 37px 43px 59px 32px;
}

.sidebar__i-list-top {
    padding: 0;
}

.sidebar__t-text {
    color: #9D9FAC;
    transition: all 0.4s ease;
}

.sidebar__t-item {
    display: flex;
    margin: 0;
    align-items: center;
    gap: 24px;
    padding: 18px 24px 18px 32px;
    fill: #60626E;
    transition: all 0.4s ease;
    &.active {
        color: #dde2ff;
        border-left: 1px solid #dde2ff;
        background-color: rgb(159, 162, 180, 0.08);
        fill: #fff;
        &svg{
            fill: rgb(255, 255, 255);
            &fill {
                opacity: 1;
            }
        }
    }
    
}

.sidebar__item {
    transition: all 0.3s ease;
}

.active {
    fill: #fff;
}

svg {
    opacity: 1;
}

.sidebar__i-list-bottom {
    margin-top: 36px;
    padding-top: 34px;
    border-top: 1px solid #40414A;
    gap: 24px;
    padding: 0;
    
}

.sidebar__b-item {
    padding: 18px auto 18px 32px;
    display: flex;
    align-items: center;
    gap: 25px;
    padding: 18px 24px 18px 32px;
    
    
}