.container {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}

body {
    transition: all 0.4s ease;

}

.dark__mode {
    background-color: #59595a;
    padding-left: 100px;
    &.header__btn {
        left: 1px;
    }
}

.layout {
    width: 100%;
    padding-right: 33px;
    padding-left: 30px;
    display: flex;
}

.app {
    display: flex;
    height: 100%;
}

h2 {
    position: absolute;
    top: 19px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 0.3px;
    transition: all 0s ease;
}

a , p , ul , li , div , svg {
    list-style-type: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
    transition: all 0.4s ease;
} 

